import { Atividade } from './atividade.model';
import { Base } from './base.model';
import { PlanoEntrega } from './plano-entrega.model';
import { PlanoTrabalhoConsolidacao } from './plano-trabalho-consolidacao.model';
import { PlanoTrabalho } from './plano-trabalho.model';
import { Usuario } from './usuario.model';

export class Status extends Base {
    public usuario?: Usuario; /* Usurio que promoveu a mudana de status */
    public plano_entrega?: PlanoEntrega; /* Plano de Entregas que sofreu a mudana de status */
    public plano_trabalho?: PlanoTrabalho; /* Plano de Trabalho que sofreu a mudana de status */
    public plano_trabalho_consolidacao?: PlanoTrabalhoConsolidacao; /* Consolidao de Plano de Trabalho que sofreu a mudana de status */
    public atividade?: Atividade; /* Atividade que sofreu a mudana de status */

    public codigo: string = ""; /* Nome do status */
    public justificativa: string = ""; /* Justificativa da mudana para o status atual */

    public constructor(data?: any) { super(); this.initialization(data); }
}